main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: absolute;
  top: 40px;
  left: 40px;
  width: 320px;
  height: 320px;
  background: #F5F5F5;
  border-radius: 10px;
  gap: 15px;
  padding: 20px;
}

.grid-container {
  display: grid;
  grid-template-columns: repeat(5, 1fr);
  grid-template-rows: auto auto;
  gap: 20px;
}

.die {
  height: 40px;
  width: 40px;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
  border-radius: 3.81277px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer; 
}  

.die-num {
  font-size: 2rem;
}

button {
  display: block;
  background: blue;
  color: white;
  border-radius: 5px;
  border: none;
  height: 40px;
  width: 100px;
  font-size: 1rem;
  font-family: 'Karla', sans-serif;
  cursor: pointer;
  margin-top: 5px;
}

.button:focus {
  outline: none;
  
}

button:active {
  box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}

.green {
  background-color: #59E391;
}

.title {
   margin: 0;
   padding: 0;
}

.instructions {
  margin: 0 0 15px 0;
  padding: 0;
  font-size: small;
  text-align: center;
}